}

.timeline_events{  
  position: relative;
  max-width: 640px;
  margin: auto;
}

.timeline_events:before{
  position: absolute;
  display: block;
  content: '';
  width: .2rem;
  left: -0.3rem;
  top: 1rem;
  bottom: -1.5rem;
  background: #ededed;
}

.timeline_events:last-child:before {
  display: none;
}
.timeline .timeline_events>span{
  display: block;
    width: 1rem;
    height: 1rem;
    position: absolute;
    top: 0rem;
